import React, { Component } from 'react'

// import Kamusta from '../Images/kamusta.png'
import AdornataLeftCenter from './Images/Adornata/AdornataLeftCenter.png'
import AdornataMidTop from './Images/Adornata/AdornataMidTop.png'
import AdornataRightBott from './Images/Adornata/AdornataRightBott.png'


import ATRLeftCenter from './Images/ATR/ATRLeftCenter.png'
import ATRMidTop from './Images/ATR/ATRMidTop.png'
import ATRRightBott from './Images/ATR/ATRRightBott.png'


import BookingAppLeftCenter from './Images/BookingApp/BookingAppLeftCenter.png'
import BookingAppMidTop from './Images/BookingApp/BookingAppMidTop.png'
import BookingAppRightBott from './Images/BookingApp/BookingAppRightBott.png'


import RentalLeftCenter from './Images/Rental/RentalLeftCenter.png'
import RentalMidTop from './Images/Rental/RentalMidTop.png'
import RentalRightBott from './Images/Rental/RentalRightBott.png'



let slide = 
[
  {
    title: 'Adornata',
    imageOne: AdornataLeftCenter,
    imageTwo: AdornataMidTop,
    imageThree: AdornataRightBott,
  },
  {
    title: 'Booking App',
    imageOne: BookingAppLeftCenter,
    imageTwo: BookingAppMidTop,
    imageThree: BookingAppRightBott,
  },
  {
    title: 'Rental App',
    imageOne: RentalLeftCenter,
    imageTwo: RentalMidTop,
    imageThree: RentalRightBott,
  },
  {
    title: 'ATR',
    imageOne: ATRLeftCenter,
    imageTwo: ATRMidTop,
    imageThree: ATRRightBott,
  },
]


class Work extends Component {
	constructor(props){
		super(props)
		this.state = {
      index: 0
    }

    this.addIndex = this.addIndex.bind(this)
    this.minusIndex = this.minusIndex.bind(this)

    this.keyPress = this.keyPress.bind(this)
    // this.mouseWheel = this.mouseWheel.bind(this)

	}
  addIndex(){
    let index = this.state.index
    index === slide.length - 1 ? index = slide.length - 1 : index = index + 1
    this.setState({
      index: index
    })
  }
  minusIndex(){
    let index = this.state.index
    index === 0 ? index = 0 : index = index - 1
    this.setState({
      index: index
    })
  }
  keyPress(e){
    if(e.key === 'ArrowDown'){
      this.addIndex()
    }
     if(e.key === 'ArrowUp'){
      this.minusIndex()
    }
  }
	
  render() {
    return (
      <div className='work-container' tabIndex="1" onKeyDown={this.keyPress} >
          <div className='image-one' key={this.state.index + 'one'}>
            <div className='slide-image'  style={{backgroundImage:  'url(' + slide[this.state.index].imageOne + ')'}}></div>
          </div> 

          <div className='image-two'key={this.state.index + 'two'} >
            <div className='slide-image'  style={{backgroundImage:  'url(' + slide[this.state.index].imageTwo + ')'}}></div>
          </div> 


          <div className='image-three' key={this.state.index + 'three'} >
            <div className='slide-image' style={{backgroundImage:  'url(' + slide[this.state.index].imageThree + ')'}}></div>
          </div> 
          
       

         <div  className='slide-text' key={slide[this.state.index].title}>
            <div className='slide-title'>
              {slide[this.state.index].title}
            </div>
          </div>

          <div className='slide-index'>
            <div className='arrow fa fa-angle-left' onClick={this.minusIndex}>
              <i></i>
            </div>
              0{this.state.index + 1} - 0{slide.length}
            <div className='arrow fa fa-angle-right' onClick={this.addIndex}>
              
            </div>
          </div>
      </div>

    );
  }
}

export default Work
